.skillsBlock {
    min-height: 100vh;
    background-color: black;
    padding: 50px 0;
}

.skillsContainer {
    flex-direction: column;
    justify-content: flex-start;
}

.skills {
    border: 1px solid;
    width: 100%;
    min-height: auto;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

@media screen and (max-width: 550px) {
    .skills {
        justify-content: center;
    }
}