.title {
    color: white;
    margin-top: 40px;
    margin-bottom: 30px;
    width: 100%;
    display: flex;
    justify-content: center;
    line-height: 1.2;
    letter-spacing: 5px;
    position: relative;
}

.title h2 {
    font-family: "Bebas Neue", cursive;
    font-weight: 300;
    color: #fff;
    text-transform: capitalize;
    margin: 0;
    font-size: 35px;
}

.title::before {
    content: "";
    position: absolute;
    background: red;
    width: 100px;
    height: 2px;
    bottom: -5px;
    left: 50%;
    transform: translate(-50%, 50%);
}

